$(document).ready(function() {
        // [ ÌöåÏõêÍ∞ÄÏûÖ ]
        $('#signUpForm').on('submit', function (event) {
            console.log("test")
            event.preventDefault(); // Í∏∞Î≥∏ Ìèº Ï†úÏ∂ú ÎèôÏûëÏùÑ ÎßâÏùå
    
            const requestData = {
                password: $('#password').val(),
                email: $('#email').val(),
                name: $('#name').val(),
                nickname: $('#nickName').val(),
                phoneNumber: $('#phoneNumber').val(),
                address: $('#address').val() + " " + $('#address2').val(),
            };
    
            $.ajax({
                url: `${window.SERVER_URL}/users`,
                type: 'POST',
                contentType: 'application/json',
                data: JSON.stringify(requestData),
                xhrFields: {
                    withCredentials: true // ÌïÑÏöî Ïãú Ï∂îÍ∞Ä
                },
                crossDomain: true,
                success: function (response) {
                    if (response.status === 200) {
                        alert('ÌöåÏõê Í∞ÄÏûÖÏóê ÏÑ±Í≥µÌïòÏòÄÏäµÎãàÎã§.');
                        window.location.href = '/login';
                    }
                },
                error: function (jqXHR) {
                    console.log(jqXHR)
                    const responseValidation = jqXHR.responseText;
                    const response = jqXHR.responseJSON;
                    if (response != null) {
                        alert(response.message);
                    } else {
                        alert(responseValidation);
                    }
    
                }
            });
        });

        //Îã§Ïùå Ï£ºÏÜå API
        let isAddressWindowOpen = false;

        $('#address').on('click', function() {
            if (isAddressWindowOpen) return; // Ï∞ΩÏù¥ Ïó¥Î†§ ÏûàÏúºÎ©¥ ÌÅ¥Î¶≠ Î¨¥Ïãú
    
            isAddressWindowOpen = true; // Ï∞ΩÏù¥ Ïó¥Î†§ ÏûàÏùåÏùÑ ÌëúÏãú
    
            new daum.Postcode({
                oncomplete: function(data) {
                    var address = data.roadAddress;
                    $('#address').val(address);
                    isAddressWindowOpen = false; // Ï∞ΩÏù¥ Îã´ÌòîÏùåÏùÑ ÌëúÏãú
                },
                onclose: function() {
                    isAddressWindowOpen = false; // Ï∞ΩÏù¥ Îã´ÌòîÏùåÏùÑ ÌëúÏãú
                }
            }).open();
        });

        const $submitBtn = $('#submitBtn');
        const $inputs = $('#signUpForm input[required]');

        function checkInputs() {
            const allFilled = $inputs.toArray().every(input => $(input).val().trim() !== '');
            $submitBtn.prop('disabled', !allFilled);
        }

        $inputs.on('input', checkInputs);

        // Ï¥àÍ∏∞ Í≤ÄÏÇ¨
        checkInputs();

        //Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        $('#navigateButton').click(function () {
            window.location.href = '/login';
        });
});